import React, { Component } from 'react';
import '../App.css';
export class Notification extends Component {
  constructor() {
    super();
    this.isallAlerts = true;
    this.isonlyTask = true;
    this.isfailTask = true;
    this.isnoAlerts = true;
    this.iscancelornot = true;
    this.notificationObj = "";
    this.allAlertsoN = React.createRef();
    this.allAlertsoFF = React.createRef();
    this.onlyTaskoN = React.createRef();
    this.onlyTaskoFF = React.createRef();
    this.failTaskoN = React.createRef();
    this.failTaskoFF = React.createRef();
    this.noAlertsoN = React.createRef();
    this.noAlertsoFF = React.createRef();
    this.msg = React.createRef();
    this.state = {
      isCancel: this.iscancelornot
    }
  }
  cancelNotificaton() {
    this.iscancelornot = false
    this.setState({
      isCancel: this.iscancelornot
    });
  }
  oN(value) {
    if (value === "allAlertsoN") {
      this.allAlertsoFF.current.style.display = "block";
      this.allAlertsoN.current.style.display = "none";
      this.isallAlerts = false;

    } else if (value === "onlyTaskoN") {
      this.onlyTaskoFF.current.style.display = "block";
      this.onlyTaskoN.current.style.display = "none";
      this.isonlyTask = false;
    }
    else if (value === "failTaskoN") {
      this.failTaskoFF.current.style.display = "block";
      this.failTaskoN.current.style.display = "none";
      this.isfailTask = false;
    } else if (value === "noAlertsoN") {
      this.noAlertsoFF.current.style.display = "block";
      this.noAlertsoN.current.style.display = "none";
      this.isnoAlerts = false;
    }

  }
  oFF(value) {
    if (value === "allAlertsoFF") {
      this.allAlertsoFF.current.style.display = "none";
      this.allAlertsoN.current.style.display = "block";
      this.isallAlerts = true;
    } else if (value === "onlyTaskoFF") {
      this.onlyTaskoFF.current.style.display = "none";
      this.onlyTaskoN.current.style.display = "block";
      this.isonlyTask = true;
    }
    else if (value === "failTaskoFF") {
      this.failTaskoFF.current.style.display = "none";
      this.failTaskoN.current.style.display = "block";
      this.isfailTask = true;
    } else if (value === "noAlertsoFF") {
      this.noAlertsoFF.current.style.display = "none";
      this.noAlertsoN.current.style.display = "block";
      this.isnoAlerts = true;
    }
  }
  loadNotification(notificationObj) {
    if (notificationObj.allAlerts) {
      this.allAlertsoFF.current.style.display = "none";
      this.allAlertsoN.current.style.display = "block";
      this.isallAlerts = true;
    } else {
      this.allAlertsoFF.current.style.display = "block";
      this.allAlertsoN.current.style.display = "none";
    }
    if (notificationObj.onlyTask) {
      this.onlyTaskoFF.current.style.display = "none";
      this.onlyTaskoN.current.style.display = "block";

    } else {
      this.onlyTaskoFF.current.style.display = "block";
      this.onlyTaskoN.current.style.display = "none";
    }
    if (notificationObj.failTask) {
      this.failTaskoFF.current.style.display = "none";
      this.failTaskoN.current.style.display = "block";
    } else {
      this.failTaskoFF.current.style.display = "block";
      this.failTaskoN.current.style.display = "none";
    }
    if (notificationObj.failTask) {
      this.noAlertsoFF.current.style.display = "none";
      this.noAlertsoN.current.style.display = "block";
    } else {
      this.noAlertsoFF.current.style.display = "block";
      this.noAlertsoN.current.style.display = "none";
    }
  }
  NotificationSubmit() {
    let NotificationData = { allAlerts: this.isallAlerts, onlyTask: this.isonlyTask, failTask: this.isfailTask, noAlert: this.isnoAlerts }
    localStorage.setItem("NotificationData", JSON.stringify(NotificationData));
    this.msg.current.innerHTML = "Submitted Successully";

  }
  componentDidMount() {
    let notificationJson = localStorage.getItem("NotificationData");
    this.notificationObj = JSON.parse(notificationJson);
    if (this.notificationObj !== null) {
      this.loadNotification(this.notificationObj);
    }
  }
  render() {
    if (this.state.isCancel) {
      return (
        < div >
          <span className="title">Notification</span>
          <div className="notificationinfo">
            <span className="msg" ref={this.msg}></span>
            <div className="row">
              <div className="ncol-1">
                <span>Send All Alerts</span>
                <span>Alert only task success</span>
                <span>Alerts only for failed task</span>
                <span>I do not want any alerts</span>
              </div>
              <div className="ncol-2">
                <div className="checkbox-btn">
                  <div className="row"> <div className="ON" ref={this.allAlertsoN} onClick={() => this.oN("allAlertsoN")}><span>ON</span></div><div className="OFF" ref={this.allAlertsoFF} onClick={() => this.oFF("allAlertsoFF")}><span>OFF</span></div></div>
                </div>
                <div className="checkbox-btn">
                  <div className="row"> <div className="ON" ref={this.onlyTaskoN} onClick={() => this.oN("onlyTaskoN")}><span>ON</span></div><div className="OFF" ref={this.onlyTaskoFF} onClick={() => this.oFF("onlyTaskoFF")}><span>OFF</span></div></div>
                </div>
                <div className="checkbox-btn">
                  <div className="row"> <div className="ON" ref={this.failTaskoN} onClick={() => this.oN("failTaskoN")}><span>ON</span></div><div className="OFF" ref={this.failTaskoFF} onClick={() => this.oFF("failTaskoFF")}><span>OFF</span></div></div>
                </div>
                <div className="checkbox-btn">
                  <div className="row"> <div className="ON" ref={this.noAlertsoN} onClick={() => this.oN("noAlertsoN")}><span>ON</span></div><div className="OFF" ref={this.noAlertsoFF} onClick={() => this.oFF("noAlertsoFF")}><span>OFF</span></div></div>
                </div>
              </div>
            </div>
            <ul><li className="submitbtn" onClick={() => this.NotificationSubmit()}>Submit</li><li className="cancelbtn" onClick={() => this.cancelNotificaton()}>Cancel</li></ul>
          </div>
        </div >
      );
    } else {
      return <div></div>
    }
  }
}