import React, { Component } from 'react'
export class Usermenu extends Component {
  constructor() {
    super()
    this.settingbtn = React.createRef();
    this.notificationbtn = React.createRef();
    this.task1 = React.createRef();
    this.task2 = React.createRef();
    this.state = {

    }
  }

  profilelistRendor() {
    this.settingbtn.current.style.display = "block";
    this.notificationbtn.current.style.display = "block";
  }
  taskRendor() {
    this.task1.current.style.display = "block";
    this.task2.current.style.display = "block";
  }
  profileData(value) {
    if (value === "setting") {
      let settinginfo = this.settinginfo = "settinginfo";
      this.props.isClick(settinginfo);
    } else if (value === "notification") {
      let notificationinfo = this.settinginfo = "notificationinfo";
      this.props.isClick(notificationinfo);
    } else if (value === "task") {
      let data = this.settinginfo = "data";
      this.props.isClick(data);
    }
  }
  render() {
    return (
      <dl><dt><button className="profile" onClick={() => this.profilelistRendor()}>Profile</button></dt>
        <dd><button className="common profilelist setting" ref={this.settingbtn} onClick={() => this.profileData("setting")}>Setting</button></dd>
        <dd><button className="common profilelist" ref={this.notificationbtn} onClick={() => this.profileData("notification")}>Notification</button></dd>
        <dt>
          <button className="mytasks" onClick={() => this.taskRendor()}>My Tasks</button></dt>
        <dd><button className="common mytaskslist" ref={this.task1} onClick={() => this.profileData("task")}>Task1</button></dd>
        <dd><button className="common mytaskslist" ref={this.task2} onClick={() => { }}>Task2</button></dd>
      </dl>
    )
  }
}