import React, { Component } from 'react';
import '../App.css';
export class Task extends Component {
  constructor() {
    super();
    this.iscancelornot = true;
    this.sourceName = React.createRef();
    this.targetResult = React.createRef();
    this.sql = React.createRef();
    this.tmsg = React.createRef();
    this.checkbox = React.createRef();
    this.checkflag = "";
    this.TaskDataObj = "";
    this.state = {
      isCancel: this.iscancelornot,
      checkboxValue: false
    }
  }
  cancelTask() {
    this.iscancelornot = false
    this.setState({
      isCancel: this.iscancelornot
    });
  }
  getChckeboxValue(event) {
    this.setState({
      checkboxValue: !this.state.checkboxValue
    })
  }
  loadTask(TaskDataObj) {
    this.sql.current.value = TaskDataObj.sql;
    this.checkbox.current.checked = TaskDataObj.enablelogin;
  }
  submitTask() {
    let sname = this.sourceName.value;
    let tresult = this.targetResult.value;
    let sql = this.sql.current.value;
    let TaskData = { sourceName: sname, enablelogin: this.state.checkboxValue, sql: sql, targetResult: tresult }
    localStorage.setItem("TaskData", JSON.stringify(TaskData));
    this.tmsg.current.innerHTML = "Submitted Successfully";
  }
  componentDidMount() {
    let TaskData = localStorage.getItem("TaskData");
    this.TaskDataObj = JSON.parse(TaskData);
    if (this.TaskDataObj !== null) {
      this.loadTask(this.TaskDataObj);
    }
  }
  render() {
    if (this.state.isCancel) {
      return (
        <div>
          <span className="title">Task</span>
          <div className="taskinfo">
            <p className="msg" ref={this.tmsg}></p>
            <span>Source Name</span>
            <select ref={(input) => this.sourceName = input}>
              <option selected disabled hidden>Select</option>
              <option value="sname1">sname1</option>
              <option value="sname2">sname2</option>
              <option value="sname3">sname3</option>
              <option value="sname4">sname4</option>
              <option value="sname5">sname5</option>
            </select>
            <span>Enable logging</span><input ref={this.checkbox} type="checkbox" onClick={this.getChckeboxValue.bind(this)}></input>
            <span>Provide Sql</span><textarea placeholder="write sql here" ref={this.sql}></textarea>
            <span>Target Result</span>
            <select ref={(input) => this.targetResult = input} >
              <option selected disabled hidden>Select</option>
              <option value="rresult1">rresult1</option>
              <option value="rresult2">rresult2</option>
              <option value="rresult3">rresult3</option>
              <option value="rresult4">rresult4</option>
              <option value="rresult5">rresult5</option>
            </select>
            <ul><li className="validate">Validate</li><li className="submitbtn" onClick={() => this.submitTask()}>Submit</li><li className="cancelbtn" onClick={() => this.cancelTask()}>Cancel</li></ul>
          </div>
        </div >
      );
    } else {
      return <div></div>
    }
  }
}
