import React, { Component } from 'react';
import '../App.css';
import { User } from '../component/user';
import { Usermenu } from '../component/usermenu';
import { Profile } from '../component/profile';
import { Notification } from '../component/notification';
import { Task } from '../component/task';
export class Main extends Component {
  constructor() {
    super()
    this.state = {
      data: ""
    }
  }


  isClickornot(isClickValue) {
    if (isClickValue === "settinginfo") {
      this.setState({
        data: <Profile />
      });
    } else if (isClickValue === "notificationinfo") {
      this.setState({
        data: <Notification />
      });
    } else if (isClickValue === "data") {
      this.setState({
        data: <Task />
      });
    }
  }

  render() {
    return (
      <div className="container" >
        <section>
          <User my="ravi" />
          <div className="row">
            <div className="col-1">
              <Usermenu isClick={this.isClickornot.bind(this)} />
            </div>
            <div className="col-2">
              {this.state.data}
            </div>
          </div>
        </section>
      </div >
    )
  }

}
