import React, { Component } from 'react';
import '../App.css';
export class Profile extends Component {
  constructor() {
    super();
    this.DOB = React.createRef();
    this.name = React.createRef();
    this.profession = React.createRef();
    this.designation = React.createRef();
    this.pmsg = React.createRef();
    this.dobvalidate = false;
    this.namevalidate = false;
    this.professionvalidate = false;
    this.designationvalidate = false;
    this.iscancelornot = true;
    this.dataObj = "";
    this.state = {
      isCancel: this.iscancelornot
    }
  }
  ValidFormData(value) {
    if (value === "DOB") {
      let regEx = /^\d{4}-\d{2}-\d{2}$/;
      this.dobvalidate = regEx.test(this.DOB.current.value);
      if (this.dobvalidate) {
        this.DOB.current.style.borderColor = "green";
      } else {
        this.DOB.current.style.borderColor = "red";
      }
    } else if (value === "name") {
      let regex = /^[a-zA-Z ]{3,34}$/
      this.namevalidate = regex.test(this.name.current.value);
      if (this.namevalidate) {
        this.name.current.style.borderColor = "green";
      } else {
        this.name.current.style.borderColor = "red";
      }
    }
    else if (value === "profession") {
      console.log("in pro");

      let regex = /^[a-zA-Z ]{3,34}$/
      this.professionvalidate = regex.test(this.profession.current.value);
      console.log(this.professionvalidate);

      if (this.professionvalidate) {
        this.profession.current.style.borderColor = "green";
      } else {
        this.profession.current.style.borderColor = "red";
      }
    } else if (value === "designation") {
      let regex = /^[a-zA-Z ]{3,34}$/
      this.designationvalidate = regex.test(this.designation.current.value);
      if (this.designationvalidate) {
        this.designation.current.style.borderColor = "green";
      } else {
        this.designation.current.style.borderColor = "red";
      }
    }
  }
  profileSubmit() {
    if (this.dobvalidate && this.name && this.profession && this.designation) {
      let data = { name: this.name.current.value, dob: this.DOB.current.value, profession: this.profession.current.value, designation: this.designation.current.value };
      localStorage.setItem("userData", JSON.stringify(data));
      this.pmsg.current.innerHTML = "Submitted Successully";
    }
  }

  cancelProfile() {
    this.iscancelornot = false
    this.setState({
      isCancel: this.iscancelornot
    });
  }
  loadData() {
    this.name.current.value = this.dataObj.name;
    this.DOB.current.value = this.dataObj.dob;
    this.profession.current.value = this.dataObj.profession;
    this.designation.current.value = this.dataObj.designation;
  }
  componentDidMount() {
    let dataJson = localStorage.getItem("userData");
    this.dataObj = JSON.parse(dataJson);
    if (this.dataObj !== null) {
      this.loadData();
    }
  }
  render() {
    if (this.state.isCancel) {
      return (
        <div>
          <p>{this.props.name}</p>
          <span className="title">Profile Info</span>
          <div><span className="msg" ref={this.pmsg}></span><div className="profileinfo">
            <span>Name</span><input type="text" ref={this.name} onBlur={() => this.ValidFormData("name")}></input>
            <span>DOB</span><input type="text" ref={this.DOB} onBlur={() => this.ValidFormData("DOB")}></input>
            <span>Profession</span><input type="text" ref={this.profession} onBlur={() => this.ValidFormData("profession")}></input>
            <span>Designation</span><input type="text" ref={this.designation} onBlur={() => this.ValidFormData("designation")}></input>
            <ul><li className="submitbtn" onClick={() => this.profileSubmit()}>Submit</li><li className="cancelbtn" onClick={() => this.cancelProfile()}>Cancel</li></ul>
          </div></div>
        </div>
      )
    } else {
      return <div></div>
    }
  }
}
